import Vue from "vue";
import App from "./App";
import router from "./router/index";
import { Button, Select, TableColumn, Table, MessageBox, Dialog, Input, Option, Loading, Notification, Pagination } from 'element-ui';

import PaperDashboard from "./plugins/paperDashboard";
import "vue-notifyjs/themes/default.css";

Vue.use(Loading.directive);
Vue.prototype.$loading = Loading.service
Vue.prototype.$confirm = MessageBox.confirm
Vue.prototype.$notify = Notification
Vue.use(PaperDashboard);
Vue.use(Button)
Vue.use(Select)
Vue.use(TableColumn)
Vue.use(Table)
Vue.use(Input)
Vue.use(Option)
Vue.use(Pagination)
Vue.use(Dialog)


/* eslint-disable no-new */
new Vue({
    router,
    render: h => h(App)
}).$mount("#app");