export default {
  data() {
    return {
      searchAgent: '',
      searchNum: '',
      dataTotal: 0,
      searchPageSize: 10,
      searchPage: 1,
      editTitle: '',
      showEdit: false,
      resourceData,
      editData: {},
      searchName: '',
      searchStatus: 1,
      searchLandlord: '',
      showProcess: false
    };
  },
  mounted() {
    this.getDataList()
  },
  methods: {
    openProcess(data) {
      this.editData = { ... data }
      this.showProcess = true
    },
    pageChange(page) {
      this.searchPage = page
      this.getDataList()
    },
    pageSizeChange(pageSize) {
      this.searchPage = 1
      this.searchPageSize = pageSize
      this.getDataList()
    },
    save() {
      const loading = this.$loading({background: 'rgba(0,0,0,.4)'})
      setTimeout(() => {loading.close(); this.$notify({
        title: '提示',
        message: '保存成功!',
        type: 'success'
      })}, 500)
    },
    openAdd() {
      this.editData = {}
      this.showEdit = true
      this.editTitle = '新增'
    },
    edit(index, data) {
      this.editData = { ... data[index] }
      this.showEdit = true
      this.editTitle = '修改'
    },
    delet(index, data) {
      this.editData = { ... data[index] }
      this.$confirm('确认删除？')
      .then(_ => {
        console.log(this.editData)
      })
    },
    getDataList() {
      console.log(this.searchPageSize, this.searchPage)
      const loading = this.$loading({
        background: 'rgba(0,0,0,.3)'
      })
      setTimeout(() => {
        loading.close()
      }, 500);
    }
  },
};
</script>
