<style scoped>
.login{
    background: #212120;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-x: auto;
    overflow-y: auto;
    background-image: url(../assets/img/loginbg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.content{
    background: rgba(0, 0, 0, .7);
    padding: 45px;
    border-radius: 10px;
}
.rows{
    margin-bottom: 25px;
}
.rows input{
    background: rgba(255, 255, 255, .2);
    border: none;
    outline: none;
    width: 300px;
    height: 45px;
    border-radius: 5px;
    padding: 0 10px;
    color: #fff;
    font-size: 16px;
}
input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, .5);
    font-size: 16px;
}
.loginbtn:hover{
    background: rgba(250, 150, 0);
    color: #fff;
    border-color: rgba(250, 150, 0);
}
.loginbtn{
    width: 100%;
    height: 46px;
    border: 2px solid rgba(250, 150, 0, .7);
    border-radius: 23px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    color: rgba(250, 150, 0);
    font-size: 18px;
    cursor: pointer;
}
</style>

<template>
    <div class="login">
        <div class="content">
            <div class="rows">
                <input type="text" v-model="user" placeholder="请输入登录账号">
            </div>
            <div class="rows">
                <input type="password" v-model="password" placeholder="请输入密码">
            </div>
            <div class="loginbtn" @click="login">登  录</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Login',
    data() {
        return {
            user: '',
            password: ''
        }
    },
    methods: {
        login() {
            sessionStorage.setItem('token', '123')
            this.$router.push('/')
        }
    },
}
</script>